<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import store from './store.js'
// import {mapActions} from 'vuex'
export default {
  store,
  computed: {
    productList() {
      return store.state.productList
    }
  },
  mounted() {
    store.commit('GETPRODUCT') // this.getProduct() html
  },
  // methods: {
  //   ...mapActions([
  //     'getProduct', 
  //     'getLocal',
  //     'recover' 
  //   ])
  // },
  watch: {
    productList: {
      handler: function(newVal){
        sessionStorage.clear()
        newVal.forEach(product => {
          if(product.checkedNum != 0) {
            sessionStorage.setItem(product.productId, product.checkedNum) // 
          } // sessionStorage['productList']=(JSON.stringify(newVal))
        }) 
      }, 
      deep: true // deep
    }
  }
}
</script>

<style>
* {
    box-sizing: border-box;
}  
/* min-width */
@media screen and (min-width: 1150px) {
  body {
     margin-left: calc(100vw - 100%);   
  /*   */
  }
}
body {
  text-align: center;
}

</style>



